import { Directive, HostListener, OnDestroy, OnInit } from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router } from '@angular/router';
import { FrontendLogger, getBizNestErrorResponseMessage, IfilterProps } from '@biz-nest/core-client';
import {
  AppNameService, EventbusService, MainBarTitleService,
  scrollToTop, WindowService
} from '@front-shared-lib';
import { MailUser, navByBus, runSubscriptionsForSharedUse } from '@main-shared-lib';
import { ToastrService } from 'ngx-toastr';

@Directive()
export class MailListBase
implements OnInit, OnDestroy {
  loading = false;
  mails: MailUser[] = [];
  origMails: MailUser[] = [];
  posting = false;
  selections: string[] = [];
  isDeletingMany = false;
  cleanup?: () => void;
  getErr?: string;
  showModal = false;;
  filterProps: IfilterProps = {};
  navigateByBus = navByBus(this.eventbus, this.router).navigateByBus;

  currentActivePopupId = '';

  actions = [
    {
      label: 'Update',
      icon: 'edit',
      event: (item) => this.editMail(item)
    }
  ];

  constructor(
    protected windowService: WindowService,
    protected router: Router,
    protected eventbus: EventbusService,
    protected toastr: ToastrService,
    protected apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    protected metaTagService: Meta,
    protected titleService: Title
  ) { }

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.getMails();
    this.mainBarTitle.title = 'All Mail Accounts - Manage Mail Accounts';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: 'Easily manage and view all company mails for effective communication tracking.'
    });

    const handlers = {
    };

    const { cleanup } = runSubscriptionsForSharedUse(this.eventbus, handlers);

    this.cleanup = cleanup;
    scrollToTop(this.windowService);
  }

  openExtras(id: string) {
    if (this.currentActivePopupId === id) {
      this.currentActivePopupId = '';

      return;
    }
    this.currentActivePopupId = id;
  }

  async getMails() {
    try {
      this.loading = true;
      this.getErr = '';
      const mails = await MailUser.getAll();


      this.mails = mails;
      this.origMails = mails;
    } catch (error) {
      FrontendLogger.error(error);
    } finally {
      this.loading = false;
    }
  }

  deleteMailRel(mail?: MailUser) {
    if (!mail) {
      FrontendLogger.debug('MailListBase:deleteMailRel:: - mail is undefined');

      return;
    }


    this.deleteMail(mail);
  }


  async deleteMail(mail: MailUser) {
    this.showModal = false;
    try {
      await mail.remove();

      this.mails.length = 0;
      await this.getMails();
    } catch (error) {
      FrontendLogger.error('MailsListComponent:getMails:: - err ', error);
      this.toastr.error(getBizNestErrorResponseMessage(error), 'Failed', {
        disableTimeOut: true
      });
    }
  }

  editMail(mail: MailUser) {
    this.navigateByBus(['/mail/update'], 'updateMail', mail);
  }

  addMail() {
    this.router.navigate(['/mail/add']);
  }

  sortBy(val: string, direction: 'asc' | 'desc') {
    this.mails = this.origMails.sort((a, b) => {
      if (direction === 'asc') {
        if (a[val] > b[val]) {
          return -1;
        } else if (a[val] < b[val]) {
          return 1;
        } else {
          return 0;
        }
      } else if (a[val] > b[val]) {
        return 1;
      } else if (a[val] < b[val]) {
        return -1;
      } else {
        return 0;
      }
    });
  }

  ngOnDestroy() {
    this.cleanup?.();
  }
}
